<template>
  <div class="exam-thanks">
    <div class="thanks-card">
      <!-- 结果图标 -->
      <div class="result-icon">
        <i :class="resultIcon"></i>
      </div>
      
      <!-- 结果标题 -->
      <h2 class="result-title">{{ resultTitle }}</h2>
      
      <!-- 考试信息 -->
      <div class="exam-info">
        <div class="info-item">
          <span class="label">考试名称：</span>
          <span class="value">{{ examInfo.title }}</span>
        </div>
        <div class="info-item">
          <span class="label">考试时长：</span>
          <span class="value">{{ examInfo.duration }}分钟</span>
        </div>
        <div class="info-item">
          <span class="label">题目数量：</span>
          <span class="value">{{ examInfo.questionCount }}题</span>
        </div>
        <div class="info-item" v-if="showScore">
          <span class="label">考试得分：</span>
          <span class="value score">{{ examInfo.score }}分</span>
        </div>
      </div>
      
      <!-- 操作按钮 -->
      <div class="actions">
        <el-button type="primary" @click="goToExamList">返回考试列表</el-button>
        <el-button v-if="showViewResult" @click="viewResult">查看考试结果</el-button>
      </div>

      <!-- 倒计时提示 -->
      <div class="countdown">
        <p>页面将在 {{ countdown }} 秒后自动跳转到登录页...</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ExamThanks',
  data() {
    return {
      examInfo: {
        title: '',
        duration: 0,
        questionCount: 0,
        score: 0,
        type: '' // 'immediate'立即出分, 'delay'延迟出分
      },
      countdown: 10, // 倒计时初始值
      timer: null // 定时器
    }
  },
  computed: {
    showScore() {
      return this.examInfo.type === 'immediate'
    },
    showViewResult() {
      return this.examInfo.type === 'immediate'
    },
    resultIcon() {
      return {
        'el-icon-success': this.examInfo.type === 'immediate',
        'el-icon-finished': this.examInfo.type === 'delay',
        'text-success': this.examInfo.type === 'immediate',
        'text-info': this.examInfo.type === 'delay'
      }
    },
    resultTitle() {
      if (this.examInfo.type === 'immediate') {
        return '考试完成！'
      }
      return '试卷提交成功，请等待系统判分'
    }
  },
  created() {
    // 从路由参数获取考试信息
    const query = this.$route.query
    this.examInfo = {
      title: query.title || '',
      duration: parseInt(query.duration) || 0,
      questionCount: parseInt(query.questionCount) || 0,
      score: parseInt(query.score) || 0,
      type: query.type || 'delay'
    }

    // 启动倒计时
    this.startCountdown()
  },
  methods: {
    goToExamList() {
      // 跳转到考试列表页面
      localStorage.removeItem('token')
      localStorage.removeItem('userInfo')
      this.$router.push('/login')
    },
    viewResult() {
      this.$router.push(`/exam/detail/${this.$route.query.examId}`)
    },
    startCountdown() {
      this.timer = setInterval(() => {
        if (this.countdown > 0) {
          this.countdown--
        } else {
          clearInterval(this.timer) // 清除定时器
          this.goToExamList() // 倒计时结束后跳转
        }
      }, 1000)
    }
  },
  beforeDestroy() {
    // 组件销毁前清除定时器
    if (this.timer) {
      clearInterval(this.timer)
    }
  }
}
</script>

<style scoped>
.exam-thanks {
  min-height: calc(100vh - 60px);
  display: flex;
  justify-content: center;
  align-items: center;
  background: #f5f7fa;
  padding: 20px;
}

.thanks-card {
  background: #fff;
  border-radius: 8px;
  padding: 40px;
  text-align: center;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,0.1);
  width: 100%;
  max-width: 600px;
}

.result-icon {
  font-size: 80px;
  margin-bottom: 20px;
}

.text-success {
  color: #67C23A;
}

.text-info {
  color: #409EFF;
}

.result-title {
  font-size: 24px;
  color: #303133;
  margin-bottom: 30px;
}

.exam-info {
  text-align: left;
  margin: 30px 0;
}

.info-item {
  margin: 15px 0;
  font-size: 16px;
}

.label {
  color: #909399;
  margin-right: 10px;
}

.value {
  color: #303133;
}

.value.score {
  color: #F56C6C;
  font-size: 18px;
  font-weight: bold;
}

.actions {
  margin-top: 40px;
  display: flex;
  justify-content: center;
  gap: 20px;
}

.countdown {
  margin-top: 20px;
  font-size: 14px;
  color: #909399;
}
</style>